@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
	text-decoration: none;
}
*{
	box-sizing: border-box;
}

/* font */
body {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
}
/* navbar css */
.container {
	width: 95vw;
	margin: 0 auto;
  max-width: 100%;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 87px;
  background-color: #FBFBFB;
}

.navigation {
  display: flex;
}

.logo {
  display: flex;
}
.logocursor {
  cursor: pointer;
}

Link, li {
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #A4A2A2;
  margin-right: 50px;
  cursor: pointer;
}

.margin {
  margin: 0;
}

.box {
  width: 32px;
  height: 32px;
  background: #D6D6D6;
  border-radius: 50%;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.box:first-child{
  background: linear-gradient(180deg, rgba(62, 223, 130, 0.99) 0%, rgba(87, 194, 244, 0.99) 100%);
  position: relative;
}
.circle {
  width: 6px;
  height: 6px;
  background: #FF0000 99%;
  border-radius: 50%;
  position: absolute;
  top: -3px;
  right: -3px;
}

.box1{
  margin: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.line {
  height: 30px;
  width: 1px;
  background: #C4C4C4;
  margin-right: 20px;
}

/* sidbar css */

.buttonbar {
  display: none;
  cursor: pointer;
  border: none;
  transition: all 0.3s linear ;
  background: inherit;
}
.buttonactive {
  display: none;
}
.buttonbar:focus {
  outline: none;
  
}
.buttonbar:active {
  transform: scale(0.85);
}
.fabar {
  width: 25px;
  height: 25px;
  color: #A4A2A2;
}

.btn-active {
  display: flex;
  border: none;
  background: inherit;
  cursor: pointer;
}

.btn-active:focus {
  outline: none;
}

/* sidbar css */

.article-page {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  top: 87px;
  width: 100vw;
  transform: translate(-150%);  
  background-color: #FBFBFB;
}

.sidbaropen {
  display: flex;
  flex-direction: column;
}
.sidbaropen, li {
  margin-bottom: 5px;
}
.sidbarlogo {
  display: flex;
 
}
.show-sidebar {
  z-index: 100;
  transform: translate(0);
}

.btn-active-no {
  display: none;
}

.fatims {
  width: 25px;
  height: 25px;
  color: #A4A2A2;
}

/* responsive 1080px */
@media only screen and (max-width: 1080px) {
  
  .hiden {
    display: none;
  }
  .buttonbar {
    display: flex;
  }
  .left-side {
    display: none;
  }
  
 }
 @media only screen and (min-width: 1080px) {
  
  
  .btn-active {
    display: none;
  }
  .show-sidebar {
    transform: translate(150%);
  }
 }

 @media only screen and (max-width: 450px) {
   .sidbarlogo {
    display: flex;
    width: 120px;
    flex-wrap: wrap;
    
  }
  .sidbarlogo .box {
     margin-bottom: 5px;
  }
  .sidbarlogo .box1 {
    display: flex;
    width: 32px;
    height: 32px;
    justify-content: center;
    align-items: center;
  }
  
 }

